.snippet {
  width: 90px;
  height: 60px;
  border: 1px solid black;
  display: grid;
  grid-template-columns: 1fr;
  -webkit-align-content: center;
          align-content: center;
  text-align: center;
  background: white;
}

.snippet:hover {
  background: transparent;
}

.snippet h2 {
  margin: 0 auto;
}

.display-container {
  grid-area: 3 / 2 / 4 / 5;
  grid-template-columns: 1fr 1fr 1fr;
  width: 100%;
  display: grid;
  margin-top: 6vh;
  grid-row-gap: 3vh;
  row-gap: 3vh;
  justify-items: center;
}

.form {
  width: 100%;
  display: grid;
  grid-area: 2 / 2 / 3 / 5;
  grid-template-columns: 1fr 33%;
  grid-gap: 3vh 5vw;
}

#daily-post-input {
  grid-area: 2 / 1 / 5 / 2;
}

.input-label {
  grid-column-start: 2;
}

.input {
  grid-column-start: 2;
  border: 1px solid black;
  border-radius: 5px;
}

.btn {
  background-color: black; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  justify-self: end;
  margin-top: 2vh;
}

.btn:hover {
  background-color: white;
  color: black;
  border: 1px solid black;
}

textarea:focus {
  outline: none;
}

textarea {
  border: 1px solid black;
  border-radius: 5px;
}

.favorite-container {
  grid-column-start: 6;
  background-color: black;
  color: white;
  width: 25%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  text-align: center;
}

.container {
  display: grid;
  grid-template-columns: 10% 1fr 1fr 1fr 10% 20%;
  grid-template-rows: auto;
  justify-items: center;
  padding: 15vh;
}

.container h1 {
  grid-area: 1 / 3 / 2/ 4;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

p,
label,
input,
textarea,
h1,
h2,
h3,
h4,
h5,
h6,
a {
  font-family: 'Open Sans Condensed', sans-serif;
}

.background {
  width: 80vw;
  position: absolute;
  z-index: -1;
}

